import React, { useState, useEffect } from "react";
import { Input, Row, Col, Card, Form, Select, message, Button } from "antd";
import Flex from "components/shared-components/Flex";
import OrganizationApi from "api/organization-api";
import { useHistory } from "react-router-dom";

const { Option } = Select;

const rules = {
  name: [
    {
      required: true,
      message: "Nhp vo ni dung cu hi",
    },
  ],
};

const GeneralField = (props) => {
  const [item, setItem] = useState(props.item);
  const [submitLoading, setSubmitLoading] = useState(false);

  let history = useHistory();

  const handleChange = (event) => {
    const currentItem = Object.assign({}, item);
    const targetValue =
      event.target.type === "checkbox"
        ? event.target.checked
        : event.target.value;
    currentItem[event.target.name] = targetValue;
    setItem({ ...currentItem });
  };

  const handleSelectChange = (field, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[field] = value;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange(currentItem);
    }
  };

  const onFinish = () => {
    setSubmitLoading(true);
    new OrganizationApi().save(item).then((response) => {
      setSubmitLoading(false);
      if (item.id === 0) {
        message.success(`To mi thnh cng.`);
        history.push(`/lms/modules/system/classes/edit/${response.data.id}`);
      }
      if (item.id > 0) {
        message.success(`Cp nht thnh cng.`);
      }
    });
  };

  useEffect(() => {
    setItem(props.item);
  }, [props.item]);

  return (
    item && (
      <Row gutter={16}>
        <Col span={24}>
          <Flex justifyContent="between">
            <div className="mb-3">&nbsp;</div>
            <div className="mb-3">
              <Button
                type="primary"
                onClick={() => onFinish()}
                loading={submitLoading}
              >
                Cp nht
              </Button>
            </div>
          </Flex>
        </Col>
        <Col xs={24} sm={24} md={17}>
          <Card title="Thng tin">
            <Form.Item label="Tn lp" rules={rules.name}>
              <Input
                name="name"
                value={item.name}
                onChange={handleChange}
                defaultValue={props.item?.name}
              />
            </Form.Item>
            <Form.Item label="Khi">
              <Select
                placeholder="Chn Khi"
                defaultValue={item.group}
                value={item.group}
                name="group"
                onChange={(value) => handleSelectChange("group", value)}
              >
                <Option value={0}>Chn</Option>
                <Option value={1}>Khi 1</Option>
                <Option value={2}>Khi 2</Option>
                <Option value={3}>Khi 3</Option>
                <Option value={4}>Khi 4</Option>
                <Option value={5}>Khi 5</Option>
                <Option value={6}>Khi 6</Option>
                <Option value={7}>Khi 7</Option>
                <Option value={8}>Khi 8</Option>
                <Option value={9}>Khi 9</Option>
                <Option value={10}>Khi 10</Option>
                <Option value={11}>Khi 11</Option>
                <Option value={12}>Khi 12</Option>
              </Select>
            </Form.Item>
            <Form.Item label="M t">
              <Input.TextArea
                name="description"
                value={item.description}
                onChange={handleChange}
                defaultValue={props.item?.description}
                rows={4}
              />
            </Form.Item>
          </Card>
        </Col>
        <Col xs={24} sm={24} md={7}>
          <Card title="nh i din">
          </Card>
        </Col>
      </Row>
    )
  );
};

export default GeneralField;
